<!--
 * @Description: 数据字典列表展示
 * @Autor: Zhongyu
 * @Date: 2021-05-07 10:26:43
 * @LastEditors: Zhongyu
 * @LastEditTime: 2021-05-19 13:58:31
-->
<template>
  <div class="roles">
    <box>
      <div class="search">
        <!-- 搜索栏 start -->
        <div class="search-body">
          <!-- 表单 layout="inline" 横向自适应布局 -->
          <a-form-model
            class="default-search-form"
            layout="inline"
            :model="searchData"
            ref="ruleForm"
            @submit="handleSubmit"
          >
            <a-form-model-item label="角色名称：" prop="roleName">
              <a-input
                v-model="searchData.roleName"
                class="default-input"
                placeholder="请输入"
              ></a-input>
            </a-form-model-item>
            <a-form-model-item label="创建时间：" prop="time">
              <a-range-picker
                separator=""
                show-time
                v-model="searchData.time"
                dropdown-class-name="default-range-picker-dropdown"
                class="default-range-picker"
                :get-calendar-container="triggerNode => triggerNode.parentNode"
              />
            </a-form-model-item>
          </a-form-model> 
        </div>
        <!-- 搜索栏 end -->
        <div class="operation-btn">
            <a-button
                class="operation-button"
                type="primary"
                html-type="submit"
              >
                <a-icon type="search" /> 搜索
              </a-button>
              <a-button
                class="operation-button rear-butoon"
                type="primary"
                @click="resetForm"
              >
                <a-icon type="reload" /> 重置
              </a-button>
          <a-button
            class="operation-button rear-butoon"
            type="primary"
            @click="addRole"
          >
            <a-icon type="form" /> 新增
          </a-button>
          <a-button
            class="operation-button rear-butoon"
            type="primary"
            @click="deleteRole"
          >
            <a-icon type="delete" /> 批量删除
          </a-button>
        </div>
      </div>

      <!-- 内容区域 start -->
      <div class="content-body">
        <!-- 表格 start -->
        <a-table
          class="default-table"
          :pagination="false"
          :columns="columns"
          :data-source="datas"
          rowKey="no"
          :row-selection="{ selectedRowKeys: selectedRowKeys, onChange: onSelectChange }"
          :scroll="{ y: 'calc(90vh - 16.85vw)' }"
        >
          <span slot="operation" class="operation" slot-scope="text, record">
            <div class="count" @click="editRole(record)">
              <a-icon type="form" />
              <a>编辑</a>
            </div>
            <div class="count" @click="getRole(record)">
              <a-icon type="aliwangwang" />
              <a>查看</a>
            </div>
            <div class="count" @click="deleteRole(record)">
              <a-icon type="close" />
              <a>删除</a>
            </div>
          </span>
        </a-table>
        <!-- 表格 end -->
      </div>
      <!-- 内容区域 end -->

      <!-- 分页器 start -->
      <div class="content-pagination">
        <pagination
          ref="pagination"
          :total="500"
          @change="onPaginationChange"
        />
      </div>
      <!-- 分页器 end -->
    </box>
    <roles-edit
      ref="roleEdit"
      :visible="editVisible"
      @close="setEditVisible(false)"
    ></roles-edit>
    <roles-delete
      ref="roleDelete"
      :visible="deleteVisible"
      @close="closeDelete"
    ></roles-delete>
  </div>
</template>

<script>
import Box from "../../components/Box";
import Pagination from "../../components/Pagination";

import RolesEdit from "./rolesEdit";
import RolesDelete from "./rolesDelete";

/**
 * 表头 // details
 */
const columns = [
  {
    title: "角色名称",
    key: "roleName",
    dataIndex: "roleName"
  },
  {
    title: "角色描述",
    dataIndex: "roleDesc",
    key: "roleDesc"
  },
  {
    title: "创建时间",
    dataIndex: "createTime",
    key: "createTime"
  },
  {
    title: "操作",
    dataIndex: "operation",
    key: "operation",
    width: "15vw",
    scopedSlots: { customRender: "operation" }
  }
];

export default {
  components: {
    Box, // 大边框
    Pagination, // 分页器
    RolesEdit, // 新增，修改
    RolesDelete // 删除
  },
  data() {
    return {
      columns: columns, // 表格表头
      datas: [], // 表格数据
      editVisible: false, // 是否显示弹框
      deleteVisible: false, // 是否显示删除弹框
      modalTitle: "新增信息", // 弹框头文字
      pageSize: 10, // 每页条数
      page: 1, // 当前页
      selectedRowKeys: [], // Check here to configure the default column
      // 搜索栏值
      searchData: {
        roleName: "",
        time: null
      }
    };
  },
  computed: {
    hasSelected() {
      return this.selectedRowKeys.length > 0;
    },
  },
  mounted() {
    //设置分页数据
    this.$refs.pagination.setData(this.page, this.pageSize);
    // 加载表格数据
    this.setList();
  },

  methods: {
    setList() {
      let datas = [];
      for (let i = 0; i < this.pageSize; i++) {
        let item = {
          no: i + 1, // 序号
          roleName: "Admin",
          roleDesc: "超级管理员", 
          createTime: "2020-01-02 12:00:30"
        };
        datas.push(item);
      }
      this.datas = datas;
    },

    onSelectChange(selectedRowKeys) {
      console.log('selectedRowKeys changed: ', selectedRowKeys);
      this.selectedRowKeys = selectedRowKeys;
    },

    /**
     * 分页时间
     */
    onPaginationChange(page, pageSize) {
      console.log(page, pageSize);
      this.pageSize = pageSize;
      this.setList();
    },

    /**
     * 设置弹框是否显示
     * @param {*} visible 是否显示参数
     */
    setEditVisible(visible) {
      this.editVisible = visible;
    },

    /**
     * 关闭删除弹框
     */
    closeDelete() {
      this.deleteVisible = false;
    },

    closeDetailsVisible() {
        this.detailsVisible = false;
    },

    /**
     * 添加数据字典-打开弹框
     */
    addRole() {
      this.setEditVisible(true);
      this.$refs.roleEdit.initFromData({
        dictName: "",
        dictCode: ""
      }, "新增角色");
    },

    /**
     * 编辑数据字典-打开弹框
     * @param {*} record 当前行数据
     */
    editRole(record) {
      this.setEditVisible(true);
      // 初始化弹框默认值
      this.$refs.roleEdit.initFromData(record, "编辑角色");
    },

    deleteRole() {
      this.deleteVisible = true;
    },

    getRole(record) {
      this.setEditVisible(true);
      this.$refs.roleEdit.initFromData(record, "查看角色");
    },

    /**
     * 搜索栏搜索
     */
    handleSubmit() {
      this.$refs.ruleForm.validate(valid => {
        if (valid) {
          alert("submit!");
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },

    /**
     * 搜索栏重置
     */
    resetForm() {
      this.$refs.ruleForm.resetFields();
    }
  }
};
</script>

<style lang="less">
@import "./index.less";
</style>
