<template>
  <div class="roles-edit">
    <modal
      :title="title"
      :visible="visible"
      width="46.094vw"
      height="35.625vw"
      @close="$emit('close')"
    >
      <img
        slot="modalBg"
        class="modal-bg"
        src="../../assets/img/components/img-modal-4.png"
      />
      <a-form-model ref="ruleForm" :model="form" :rules="rules">
        <a-form-model-item label="角色名称：" prop="dictName">
          <a-input
            v-model="form.dictName"
            class="default-input"
            autocomplete="off"
            :disabled="isDisabled"
          />
        </a-form-model-item>
        <a-form-model-item label="角色描述：" prop="dictCode">
          <a-input
            v-model="form.dictCode"
            class="default-input"
            autocomplete="off"
            :disabled="isDisabled"
          />
        </a-form-model-item>

        <a-form-model-item label="角色权限：" prop="dictCode">
          <a-tree
            v-model="checkedKeys"
            checkable
            multiple
            :disabled="isDisabled"
            :expanded-keys="expandedKeys"
            :auto-expand-parent="autoExpandParent"
            :selected-keys="selectedKeys"
            :tree-data="treeData"
            @expand="onExpand"
            @select="onSelect"
          >
          </a-tree>
        </a-form-model-item>
      </a-form-model>

      <div class="details-operation-btn" v-show="!isDisabled">
        <a-button class="form-button" type="default" @click="$emit('close')">
          取消
        </a-button>
        <a-button class="form-button" type="primary" @click="onSubmit">
          保存
        </a-button>
      </div>
    </modal>
  </div>
</template>

<script>
import Modal from "../../components/Modal";

import moment from "moment";

const treeData = [
  {
    title: "0-0",
    key: "0-0",

    children: [
      {
        title: "0-0-0",
        key: "0-0-0",

        children: [
          { title: "0-0-0-0", key: "0-0-0-0" },
          { title: "0-0-0-1", key: "0-0-0-1" },
          { title: "0-0-0-2", key: "0-0-0-2" }
        ]
      },
      {
        title: "0-0-1",
        key: "0-0-1",

        children: [
          { title: "0-0-1-0", key: "0-0-1-0" },
          { title: "0-0-1-1", key: "0-0-1-1" },
          { title: "0-0-1-2", key: "0-0-1-2" }
        ]
      },
      {
        title: "0-0-2",

        key: "0-0-2"
      }
    ]
  },
  {
    title: "0-1",
    key: "0-1",

    children: [
      { title: "0-1-0-0", key: "0-1-0-0" },
      { title: "0-1-0-1", key: "0-1-0-1" },
      { title: "0-1-0-2", key: "0-1-0-2" }
    ]
  },
  {
    title: "0-2",
    key: "0-2"
  }
];

export default {
  components: {
    Modal
  },
  props: {
    // 对话框是否可见
    visible: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      form: {
        dictName: "",
        dictCode: ""
      },
      title: "新增信息",
      isDisabled: false,
      expandedKeys: ["0-0-0", "0-0-1"],
      autoExpandParent: true,
      checkedKeys: ["0-0-0"],
      selectedKeys: [],
      treeData,
      rules: {
        dictName: [
          {
            required: true,
            message: "字典名称不可为空",
            trigger: "blur"
          }
        ],
        dictCode: [
          {
            required: true,
            message: "字典code不可为空",
            trigger: "blur"
          }
        ]
      }
    };
  },
  watch: {
    checkedKeys(val) {
      console.log("onCheck", val);
    }
  },
  methods: {
    moment,

    onExpand(expandedKeys) {
      this.expandedKeys = expandedKeys;
      this.autoExpandParent = false;
    },

    onCheck(checkedKeys) {
      console.log("onCheck", checkedKeys);
      this.checkedKeys = checkedKeys;
    },
    onSelect(selectedKeys, info) {
      console.log("onSelect", info);
      this.selectedKeys = selectedKeys;
    },

    /**
     * 初始化表单值
     * @param {*} data 表单参数
     * @return {*}
     */
    initFromData(data, title) {
      this.title = title;
      this.isDisabled = (title == "查看角色")
      this.form = data;
    },

    onSubmit() {
      this.$refs.ruleForm.validate(valid => {
        if (valid) {
          //   alert('submit!');
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    }
  }
};
</script>

<style lang="less" scoped></style>
