<!--
 * @Description: 弹框组件
 * @Autor: Zhongyu
 * @Date: 2021-05-09 08:33:59
 * @LastEditors: Zhongyu
 * @LastEditTime: 2021-05-17 16:14:21
-->
<template>
  <div id="modal" class="default-modal" v-if="visible">
    <!-- 弹框蒙层 start -->
    <div class="modal-mask" v-show="visibleMask" @click="maskClose"></div>
    <!-- 弹框蒙层 end -->

    <!-- 弹框内容 start -->
    <div class="modal-wrap">
      <div class="modal-content" :style="{ width: width, height: height }">
        <div class="modal-bg-img">
          <!-- 插入背景图片 -->
          <slot name="modalBg"></slot>
        </div>

        <div class="modal-head">
          <!-- 弹框标题 stsrat -->
          <div class="head-title">
            <img
              class="head-title-bg"
              src="../../assets/img/components/img-moadl-title-bg.png"
            />

            <div class="title-text">{{ title }}</div>
          </div>
          <!-- 弹框标题 end -->

          <!-- 弹框关闭按钮 start -->
          <div class="modal-close" @click="$emit('close', !visible)"></div>
          <!-- 弹框关闭按钮 end -->
        </div>
        <div class="modal-body">
          <!-- 内容插入 -->
          <slot></slot>
        </div>
      </div>
    </div>
    <!-- 弹框内容 end -->
  </div>
</template>

<script>
export default {
  name: "modal",
  model: {
    prop: "visible",
    event: "close"
  },
  props: {
    // 宽度
    width: {
      type: String || Number,
      default: "46.094vw"
    },
    // 高度
    height: {
      type: String || Number,
      default: "26.25vw"
    },
    // 标题
    title: {
      type: String,
      default: ""
    },
    // 对话框是否可见
    visible: {
      type: Boolean,
      default: false
    },
    // 点击蒙层是否允许关闭
    maskClosable: {
      type: Boolean,
      default: false
    },
    // 蒙层是否可见
    visibleMask: {
      type: Boolean,
      default: true
    }
  },

  methods: {
    /**
     * 蒙层点击事件
     */
    maskClose() {
      /**
       * 在蒙层允许关闭触发关闭回调事件
       */
      if (this.maskClosable) {
        this.$emit("close", !this.visible);
      }
    }
  }
};
</script>

<style lang="less" scoped>
@import "./index";
</style>
