<!--
 * @Description: 分页组件
 * @Autor: Zhongyu
 * @Date: 2021-05-07 18:45:30
 * @LastEditors: Zhongyu
 * @LastEditTime: 2021-05-14 10:14:24
-->
<template>
  <div class="pagination">
    <!-- 下拉当页条数切换 start -->
    <div class="page-sizes">
      <span>显示</span>
      <a-select v-model="pageSize" @change="handleSelectChange">
        <a-icon slot="suffixIcon" type="caret-down" theme="filled" />
        <a-select-option
          v-for="item in pageSizeOptions"
          :key="item"
          :value="item"
        >
          {{ item }}
        </a-select-option>
      </a-select>
      <span>项结果</span>
    </div>
    <!-- 下拉当页条数切换 end -->

    <!-- 分页按钮 start -->
    <a-pagination
      v-model="page"
      :total="total || 1"
      :page-size="pageSize"
      :item-render="itemRender"
      @change="handlePaginationChange"
    />
    <!-- 分页按钮 end -->
  </div>
</template>

<script>
export default {
  name: "ZPagination",
  props: {
    // 总条数
    total: {
      type: Number,
      default: 500
    }
  },
  data() {
    return {
      pageSizeOptions: [10, 20, 30, 40, 50], // 每页条数组
      page: 1, // 页码
      pageSize: 10 // 每页条数
    };
  },
  methods: {
    /**å
     * 提供父层调用更新页码和每页条数
     * @page 页码
     * @pageSize 每页条数
     */
    setData(page, pageSize) {
      if (page) {
        this.page = page;
      }
      // 筛选传入和每页条数是否在每页条数组中
      let pageSizeOption = this.pageSizeOptions.find(item => {
        return item == pageSize;
      });
      // 当存在赋值于每页条数上
      if (pageSizeOption) {
        this.pageSize = pageSize;
      }
    },

    /**
     * 把下一页和上一页转换成中文
     */
    itemRender(current, type, originalElement) {
      if (type === "prev") {
        return "上一页";
      } else if (type === "next") {
        return "下一页";
      } else if (type === "page") {
        return current;
      }
      return originalElement;
    },

    /**
     * 页码改变的回调，参数是改变后的页码及每页条数
     * @page 页码
     * @pageSize 每页条数
     */
    handlePaginationChange(page, pageSize) {
      /**
       * 触发父层方法
       */
      this.$emit("change", page, pageSize);
    },

    /**
     * pageSize 变化的回调
     * @pageSize 每页条数
     */
    handleSelectChange(pageSize) {
      let page = this.page;
      let totalPage = parseInt(this.total / pageSize);
      if (this.total % pageSize != 0) {
        totalPage += 1;
      }
      if (totalPage < page) {
        page = totalPage;
      }
      this.page = page;
      /**
       * 触发父层方法
       */
      this.$emit("change", page, pageSize);
    }
  }
};
</script>

<style lang="less">
@import "./index.less";
</style>
